<!--  UI 
      :  Element  . -->
<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn dark v-bind="attrs" v-on="on">   </v-btn>
    </template>
    <v-card>
      <v-card-title>
        <span class="text-h5"> </span>
      </v-card-title>

      <v-card-text>
        <v-container>
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-row no-gutters class="d-flex justify-center align-center">
              <!-- / -->
              <v-col cols="6">
                <v-text-field
                  label=""
                  :counter="10"
                  v-model="delivery.receiver"
                  :rules="nameRules"
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="5" class="ml-4">
                <v-btn v-show="isReceiver" dark @click="getMemberInfo('same')"> </v-btn>
                <v-btn v-show="!isReceiver" dark @click="getMemberInfo('remove')"
                  >  </v-btn
                >
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label=" "
                  v-model="delivery.tel"
                  :rules="telRules"
                  :counter="13"
                  placeholder="010-1234-5678"
                ></v-text-field>
              </v-col>
              <!--  -->
              <v-col cols="6">
                <v-text-field
                  label=""
                  v-model="delivery.zipcode"
                  :rules="notNullRules"
                  readonly
                ></v-text-field>
              </v-col>
              <v-col cols="5" class="ml-4">
                <v-btn dark @click="execDaumPostcode()"> </v-btn>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label=""
                  v-model="delivery.address1"
                  :rules="notNullRules"
                  readonly
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field label="" v-model="delivery.address2"></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="dialog = false"> Close </v-btn>
        <v-btn :disabled="!valid" color="blue darken-1" text @click="saveDelivery"> Save </v-btn>
      </v-card-actions>
    </v-card>
    <v-snackbar v-model="snackbar">
      {{ snackbarText }}
      <template v-slot:action="{ attrs }">
        <v-btn color="pink" text v-bind="attrs" @click="snackbar = !snackbar"> Close </v-btn>
      </template>
    </v-snackbar>
  </v-dialog>
</template>
<script>
export default {
  //component  (devTools  )
  name: "",
  //   
  components: {},
  //  
  data() {
    return {
      valid: true,
      dialog: false,
      isReceiver: true,
      delivery: {
        receiver: "",
        tel: "",
        zipcode: "",
        address1: "",
        address2: "",
      },
      nameRules: [
        (v) => !!v || " .",
        (v) => (v && v.length <= 10) || " 10  .",
        (v) => !/[~!@#$%^&*()_+|<>?:{}/]/.test(v) || "   .",
      ],
      telRules: [
        (v) => !!v || " .",
        (v) => /(\d{3})-(\d{4})-(\d{4})/.test(v) || "   .",
      ],
      notNullRules: [(v) => !!v || " ."],
      snackbarText: "",
      snackbar: false,
    };
  },
  //  
  methods: {
    execDaumPostcode() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          if (data.userSelectedType === "R") {
            //     
            this.delivery.address1 = data.roadAddress;
          } else {
            //     (J)
            this.delivery.address1 = data.jibunAddress;
          }
          //  .
          this.delivery.zipcode = data.zonecode;
        },
      }).open();
    },
    getMemberInfo(state) {
      if (state === "same") {
        this.delivery.receiver = this.oreceiver;
        this.delivery.tel = this.otel;
        this.isReceiver = !this.isReceiver;
      } else if (state === "remove") {
        this.delivery.receiver = "";
        this.delivery.tel = "";
        this.isReceiver = !this.isReceiver;
      }
    },
    saveDelivery() {
      let del = this.delivery;
      if (del.receiver === "" || del.tel === "" || del.address1 === "" || del.zipcode === "") {
        this.snackbarText = "  .";
        this.snackbar = true;
      } else {
        this.$refs.form.resetValidation();
        this.dialog = false;
        this.$emit("HandleDelivery", this.delivery);
      }
    },
  },
  created() {
    this.snackbar = false;
  },
  props: ["oreceiver", "otel"],
};
</script>
<!--scoped : ,   style .  
     -->
<style scoped></style>
